        }
    },
    methods: {
        prev() {
            this.step--;
        },
        next() {
            console.log('dd');
            this.step++
            
        },
        coreCount(){
            this.step++
            var cores=0;
            var qestions=document.getElementsByClassName("question")
            for(var i=0;i<qestions.length;i++)
            {
                var count=0;
                var flag=1;
                var answer=document.getElementsByName("q"+(i+1));
                for(var j=0;j<answer.length;j++)
                {
                    if(answer[j].checked)
                    {
                        count+=answer[j].value*1;
                        console.log(count)
                    }
                }
                if(flag==1)
                    cores+=count;
                    console.log(cores);
                if(cores >= 76){
                    document.getElementById('dectext').innerHTML='';
                    document.getElementById('highimg').src='static/.png';
                }else if(cores >= 51 && cores <= 75){
                    document.getElementById('dectext').innerHTML='';
                    document.getElementById('highimg').src='static/.png';
                }else if(cores >= 26 && cores <= 50){
                    document.getElementById('dectext').innerHTML='';
                    document.getElementById('highimg').src='static/.png';
                }else if(cores < 25){
                    document.getElementById('dectext').innerHTML='';
                    document.getElementById('highimg').src='static/.png';
                }  
            }	
        }
    },
})



</script>

